import {
	Dialog,
	DialogTitle,
	DialogContent,
	DialogActions,
	Button,
	Divider,
	Paper,
	Input,
	TextField,
	Autocomplete,
	IconButton,
	Checkbox,
	FormControlLabel,
} from "@mui/material";
import { useState } from "react";
import styled from "styled-components";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import AddIcon from "@mui/icons-material/Add";
import { useDispatch, useSelector } from "react-redux";
import { Course, CourseState, editSection, Section, addCourse, addCourseSection, setSelectedCourse } from "./logic/courses";

export type CreateScheduleDialogProps = {
	open: boolean;
	close: () => void;
};

const BigDialog = styled(Dialog)`
	&& .MuiDialog-paper {
		min-width: calc(100% - 64px);
		min-height: calc(100% - 64px);
	}
`;

const CenteredHeader = styled.div`
	display: flex;
	align-items: center;
`;

const SectionCardContainer = styled.div`
	display: flex;
	flex-wrap: wrap;
	gap: 20px;
	padding-top: 20px;
`;

const selectCurrentCourse = (state: { value: CourseState }) => state.value.selectedCourse;
const selectCourses = (state: { value: CourseState }) => state.value.courses;

export const CreateScheduleDialog = (props: CreateScheduleDialogProps) => {
	const { open, close } = props;

	const dispatch = useDispatch();
	const courses = useSelector(selectCourses);
	const currentCourse = useSelector(selectCurrentCourse);

	return (
		<BigDialog open={open}>
			<DialogTitle>
				<CenteredHeader>Create a Schedule</CenteredHeader>
			</DialogTitle>
			<Divider />
			<DialogContent>
				{currentCourse !== undefined ? (
					<>
						<Centered>
							<CourseCard goBack onClick={() => dispatch(setSelectedCourse(undefined))} />
						</Centered>
						<SectionCardContainer>
							{currentCourse.sections.map((section) => (
								<SectionCard key={section.crn} section={section} />
							))}
							<ScheduleCardPaper>
								<Group>
									<IconButton
										color="primary"
										size="large"
										onClick={() =>
											dispatch(
												addCourseSection({
													courseId: currentCourse.id,
												})
											)
										}
									>
										<AddIcon />
									</IconButton>
								</Group>
							</ScheduleCardPaper>
						</SectionCardContainer>
					</>
				) : (
					<SectionCardContainer>
						{courses.map((course, idx) => (
							<CourseCard
								key={course.id}
								goBack={false}
								onClick={() => dispatch(setSelectedCourse(course))}
							/>
						))}
						<Group>
							<IconButton
								color="primary"
								size="large"
								onClick={() => {
									const newId = `NEW-${courses.length + 1}`;
									dispatch(addCourse(newId));
								}}
							>
								<AddIcon />
							</IconButton>
						</Group>
					</SectionCardContainer>
				)}
			</DialogContent>
			<Divider />
			<DialogActions>
				<Button color="error" onClick={close}>
					Discard
				</Button>
				<Button color="inherit">Create and Save</Button>
				<Button>Create and Load</Button>
			</DialogActions>
		</BigDialog>
	);
};

const ScheduleCardPaper = styled(Paper)`
	display: flex;
	flex-direction: column;
	gap: 10px;

	padding: 10px;
	&&& {
		background: #b3d2f0;
	}
`;

const Group = styled.div`
	display: flex;
	align-items: center;
	gap: 7px;
`;

const Centered = styled.div`
	width: 100%;
	display: flex;
	justify-content: center;
`;

export const CreateScheduleCard = () => {
	const [department, setDepartment] = useState("");
	const [id, setId] = useState("");
	const [section, setSection] = useState("");
	const [name, setName] = useState("");

	const [start, setStart] = useState("");
	const [end, setEnd] = useState("");

	const [days, setDays] = useState("");
	const [profName, setProfName] = useState("");

	type func = (value: string) => void;
	const spacesAreDashes = (setter: func): func => {
		return (value: string) => {
			setter(value.replaceAll(" ", "-"));
		};
	};

	return (
		<ScheduleCardPaper elevation={5}>
			<Group>
				Course ID:
				<MyInput value={department} setValue={setDepartment} width={80} placeholder={"MATH"} />
				–
				<MyInput value={id} setValue={setId} width={80} placeholder={"1005"} />
			</Group>
			<Centered>Section A:</Centered>
			<Divider />
			<Group>
				Time:
				<MyInput value={start} setValue={setStart} width={80} placeholder={"10:00"} />
				–
				<MyInput value={end} setValue={setEnd} width={80} placeholder={"12:00"} />
				<br />
			</Group>
			<Group>
				Days: <MyInput value={days} setValue={setDays} placeholder={"Mon, Tue, Wed"} />
			</Group>
			<Group>
				Prof Name: <MyInput value={profName} setValue={setProfName} placeholder={"John Smith"} />
			</Group>
			{/* -
			<MyInput value={name} setValue={setName} width={70} placeholder={"1005"} />
			-
			<MyInput value={name} setValue={setName} width={40} placeholder={"F"} /> */}
		</ScheduleCardPaper>
	);
};

export const CourseCard = ({ goBack, onClick = () => {} }: { goBack: boolean; onClick?: () => void }) => {
	const [department, setDepartment] = useState("");
	const [id, setId] = useState("");
	return (
		<ScheduleCardPaper elevation={5}>
			<Group>
				{goBack && (
					<IconButton color="primary" aria-label="add to shopping cart" onClick={onClick}>
						<ArrowBackIosNewIcon />
					</IconButton>
				)}
				Course ID:
				<MyInput value={department} setValue={setDepartment} width={80} placeholder={"MATH"} />
				–
				<MyInput value={id} setValue={setId} width={80} placeholder={"1005"} />
			</Group>
			{!goBack && (
				<Button variant="contained" sx={{ width: "150px", margin: "auto" }} onClick={onClick}>
					Set Sections
				</Button>
			)}
		</ScheduleCardPaper>
	);
};

export const SectionCard = ({ section }: { section: Section }) => {
	const dispatch = useDispatch();

	const setSectionField = (field: keyof Section) => (value: string | boolean) => {
		dispatch(
			editSection({
				courseId: "", // You may need to pass the courseId as prop or get from context
				sectionCrn: section.crn,
				newSection: { [field]: value },
			})
		);
	};

	return (
		<ScheduleCardPaper elevation={5}>
			<Centered>
				<Group>
					Section
					<MyInput value={section.section} setValue={setSectionField("section")} width={40} placeholder={"A"} />:
				</Group>
			</Centered>
			<Divider />
			<Group>
				Time:
				<MyInput value={section.startTime} setValue={setSectionField("startTime")} width={80} placeholder={"10:00"} />
				–
				<MyInput value={section.endTime} setValue={setSectionField("endTime")} width={80} placeholder={"12:00"} />
				<br />
			</Group>
			<Group>
				Days: <MyInput value={section.days} setValue={setSectionField("days")} placeholder={"Mon, Tue, Wed"} />
			</Group>
			<Group>
				Prof Name: <MyInput value={section.profName} setValue={setSectionField("profName")} placeholder={"John Smith"} />
			</Group>
			<Group>
				<FormControlLabel
					control={
						<Checkbox
							checked={section.isOnline}
							onChange={(_, checked) => setSectionField("isOnline")(checked)}
						/>
					}
					label="Is Online"
				/>
				<FormControlLabel
					control={
						<Checkbox
							checked={section.isLab}
							onChange={(_, checked) => setSectionField("isLab")(checked)}
						/>
					}
					label="Is Lab"
				/>
			</Group>
		</ScheduleCardPaper>
	);
};

export type InputProps = {
	value: string;
	setValue: (value: string) => void;

	width?: number;
	placeholder?: string;
};

const StyledTextField = styled(TextField)`
	&&& .MuiOutlinedInput-input {
		padding: 3px 5px;
	}
`;

const MyInput = (props: InputProps) => {
	const { value, setValue, width, placeholder } = props;
	return (
		<StyledTextField
			autoComplete="off"
			sx={{ width }}
			variant="outlined"
			size="small"
			placeholder={placeholder}
			value={value}
			onChange={(e) => {
				setValue(e.target.value);
			}}
		/>
	);
};
